import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import RouteTransition from 'vue-route-transition'
Vue.use(RouteTransition)
Vue.use(ElementUI);
import 'amfe-flexible'
import {
  Tab, 
  Tabs,
  Card,
  Swipe,
  SwipeItem ,
  Lazyload ,
  Icon ,
  NavBar ,
  IndexBar,
  IndexAnchor ,
  Cell,
  List ,
  Search 
} from 'vant'
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Card);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Lazyload);
Vue.use(Icon);
Vue.use(NavBar);
Vue.use(IndexBar);
Vue.use(IndexAnchor);
Vue.use(Cell);
Vue.use(List);
Vue.use(Search);
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

