<template> 
    <div class="songBG loading" :style="{height:bgheight+'px'}"> 
        <el-image 
            class="imgs" 
            style="width: 100%; height: 100%" 
            :src="img" 
            fit="fill"> 
        </el-image> 
      <div class="musicbox"> 
          <van-nav-bar 
            ref="goback" 
            left-text="返回" 
            left-arrow 
            fixed 
            @click-left="onClickLeft" 
            /> 
          <h2 class="tag">{{title}}</h2> 
          <h2 class="author">{{author}}</h2> 
            <van-icon name="like-o" class="collect" @click="handleCollect" :color="color" />
          <div class="fixbox"></div> 
          <div class="simgbg"> 
              <img :src="img" alt="" class="revolve run"> 
          </div> 
          <audio class="Audio" :src="url" autoplay></audio> 
          <div class="Aicon"> 
              <van-icon 
                name="arrow-left" 
                size="1.5em" 
                class="left" 
                color="#ccc" 
                @click="handleprev" 
                />
              <van-icon 
                :name="Pause" 
                @click="handleChange" 
                size="1.5em" 
                color="#ccc"  
                class="center"/>
              <van-icon 
                name="arrow" 
                size="1.5em" 
                class="right"
                color="#ccc" 
                @click="handlenext"
                />
          </div>
      </div>
    </div>
</template>

<script>
import {postSong,changeSong,postCollect,ifCollect} from '../api/index'
export default {
    name:'Song', 
    data(){
        return{
            title:'',
            author:'',
            img:"",
            url:'',
            blocked:true,
            sort:'',
            Pause:'pause-circle-o',
            bgheight:0,
            color:"",
            onOff:false
        }
    },
    created(){
        let path=this.$route.path.split('/')[2]
        postSong(path)
        .then(res=>{
            this.title=res.data.data.title
            this.author=res.data.data.author
            this.img=res.data.data.img
            this.url=res.data.data.url
            this.sort=res.data.data.sort
            ifCollect(this.title)
            .then(res=>{
                this.onOff=res.data.data.onOff
                this.ifcolor()
            })
        })
        .catch((err)=>{
            console.log(err)
        })
        
        

    },
    mounted(){
        this.getWindowHeight()
        window.addEventListener("resize",this.getWindowHeight())
        
        this.handleScroll()

        let revolve=document.querySelector('.revolve')
        let Change=document.querySelector('.center')
        let ifrun=true
        let Audio=document.querySelector('audio')
        Change.onclick=function(){
            if(ifrun){
                revolve.className='zanting revolve'
                ifrun=!ifrun
                Audio.pause()
            }else{
                revolve.className='run revolve'
                ifrun=!ifrun
                Audio.play()
            }
        }
        setInterval(()=>{
            this.ifending()
        },1000)

    },
    methods:{
        ifending(){
            let Audio=document.querySelector('audio')
            window.addEventListener('ended',()=>{
                this.handlenext()
            })
        },
        handleScroll(){
            let loadingDom=document.querySelector('.loading')
            if(loadingDom.getBoundingClientRect().top<=(window.innerHeight-100)){
                loadingDom.classList.remove('loading')
            }
        },
        getWindowHeight(){
            this.bgheight=window.innerHeight;
        },
        handleChange(){
            if(this.blocked){
                this.Pause="play-circle-o"
                this.blocked=!this.blocked
            }else{
                this.Pause="pause-circle-o"
                this.blocked=!this.blocked
            }
        },
        handlenext(){
            let sort=this.sort+1
            changeSong(sort)
            .then(res=>{
                this.title=res.data.data.title
                this.author=res.data.data.author
                this.img=res.data.data.img
                this.url=res.data.data.url
                this.sort=res.data.data.sort
            })
            .catch(err=>{
                console.log(err)
            })
        },
        handleprev(){
            let sort=this.sort-1
            if(sort<=1){
                return 
            }
            changeSong(sort)
            .then(res=>{
                this.title=res.data.data.title
                this.author=res.data.data.author
                this.img=res.data.data.img
                this.url=res.data.data.url
                this.sort=res.data.data.sort
            })
            .catch(err=>{
                console.log(err)
            })
        },
        onClickLeft(){
           
            this.$router.go(-1)
        },
        handleCollect(){
            this.onOff=!this.onOff
            this.ifcolor()
            postCollect(this.title,this.author,this.onOff)
            .then(res=>{
                this.$message({
                message: res.data.msg,
                type: 'success'
                });
                this.$store.dispatch('setgetTitle',res.data.data.title)
            })
        },
        ifcolor(){
            if(this.onOff){
                this.color="red"
            }else{
                this.color=""
            }
        }
    }
}
</script>
<style lang="less">
.songBG{
    position: relative;
    width: 100%;
    transition: 0.5s;
}
.loading{
    opacity: 0;
}
.songBG .imgs{
    filter:blur(20px);
}
.musicbox{
    position: absolute;
    top: 0;
    width: 100%;
    height: 600px;
    z-index: 5;
}
.tag{
    text-align: center;
    font-size: 25px;
    color: #fff;
}
.author{
    font-size: 20px;
    color: #fff;
    text-align: center;
}
.fixbox{
    position: relative;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    background-color: rgba(137, 137, 137,0.5);
    box-shadow: 0 0 1em 0.5em rgba(189, 188, 187, 0.5);
    margin:10px auto;
}
.imgfill{
    border-radius: 50%;
}
.simgbg{
    margin:-280px auto;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    padding-bottom:300px; 
}
.simgbg .revolve{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.zanting {
    animation-fill-mode:forwards;
  }
.run {
    animation: run 5s linear infinite;
  }

.Aicon{
    letter-spacing: 30px;
    padding-top:100px; 
    width: 100%;
    text-align: center;
}
.van-nav-bar{
    font-size: 0px;
    width:30%;
    background-color: transparent;
    .van-nav-bar__text{
        font-size: 22px;
        color: #ffcd32;
    }
    .van-icon{
        font-size: 22px;
        color: #ffcd32;
    }
}
.collect{
    position: absolute;
    top: 20px;
    right: 25px;
}
@keyframes run {
    0%{
        transform:rotateZ(0deg)
    }
    100%{
        transform:rotateZ(360deg)
    }
}
</style>