<template>
    <div>
        <div class="search">
            <van-search
                :value="value"
                shape="round"
                background="#4fc08d"
                placeholder="搜索歌曲，歌手"
                :bind:change="onChange()"
                v-model="value"
                />

        <van-cell v-for="item in searchList" :title="item.title" :value="item.author" :to='/song/+item.title' />
        </div>
    </div>
</template>

<script>
import {SearchSong} from '../../api/index'
export default {
    name:'index',
    data(){
        return{
            value: '',
            searchList:[],
            to:''
        }
    },
    methods:{
        onChange(){
            let keywords=this.value.trim()
            SearchSong(keywords)
            .then(res=>{
                this.searchList=res.data.data
            })
        }
    },
}
</script>

