<template>
    <div class="recommend">
        <van-swipe class="swipe" :autoplay="3000" indicator-color="white" >
            <van-swipe-item 
                v-for="(item,index) in banner" 
                :key="index" 
                @click="handlePreview(index)"
                >
                <img :src="item" />
            </van-swipe-item>
        </van-swipe>
        <h2 class="title" >热门歌单推荐</h2>
        <van-card
            v-for="(item,index) in hot"
            @click="handleTo(item.title)"
            :key="index"
            :desc="item.desc"
            :title="item.title"
            :thumb="item.img"/>
    </div>
</template>

<script>
import {getHot} from '../../api/index'
import { ImagePreview, Swipe, Form } from 'vant';
export default {
    name:'index',
    data(){
        return{
            recommends:[],
            banner:[
                "http://p1.music.126.net/61FOOI8tOGwbgcWWi7YIKw==/109951165113382303.jpg?imageView&quality=89",
                "http://p1.music.126.net/ml0TP5xZK0J4aSve3EDL_g==/109951165114039173.jpg?imageView&quality=89",
                "http://p1.music.126.net/E-UR_Ld80NHVst3Cis9VKA==/109951165114071064.jpg?imageView&quality=89",
                "http://p1.music.126.net/_2bWujcisCeO6Lwu4JOavQ==/109951165115745785.jpg?imageView&quality=89",
                "http://p1.music.126.net/bRGj4OZdgGeoFykBJmCo5A==/109951165114184548.jpg?imageView&quality=89",
            ],
            hot:[]
        }
    },
    methods:{
       handlePreview(index){
           ImagePreview({
                images:this.banner,
                startPosition:index,
                onClose(){}
            })
       },
       handleTo(title){
           this.$router.push('/song/'+title)
       }
    },
    created(){
        getHot()
        .then(res=>{
            this.hot=res.data.data
        })
    }
}
</script>

<style lang="less">
.recommend{
    margin-top: -2px;
}
.title{
    font-size: 14px;
    text-align: center;
    padding: 15px;
    color: #ffcd32;
    background-color:rgb(41, 40, 40);
}
.swipe{
    width: 100%;
    height: 200px;
}
.swipe img{
    width: 100%;
    height: 200px;
}
.van-card{
    background-color: #222;
}
.van-card .van-card__title{
    font-size: 20px;
    line-height: 20px;
    color: #fff;
    padding: 10px;
}
.van-card .van-card__desc{
    font-size: 16px;
    color: rgb(213, 213, 213);
    padding:10px;
}

</style>