import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let types={
  SET_GETTITLE:"SET_GETTITLE",
  SET_GETAUTHOR:"SET_GETAUTHOR"
}

let state={
  title:[],
  author:[]
}
let getters={
  title:state=>state.title,
  author:state=>state.author,
}
let mutations={
  [types.SET_GETTITLE](state,title){
    if(title){
      state.title=title
    }else{
      state.title=[]
    }
  },
  [types.SET_GETAUTHOR](state,author){
    if(author){
      state.author=author
    }else{
      state.author=[]
    }
  }
}
let actions={
  setgetTitle:({commit},title)=>{
    commit(types.SET_GETTITLE,title)
  },
  setgetAuthor:({commit},author)=>{
    commit(types.SET_GETAUTHOR,author)
  }
}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters,
  actions
})
