import axios from 'axios'

axios.defaults.baseURL='http://localhost:3000';
axios.defaults.withCredentials=true;
axios.defaults.headers.post["Content-Type"]="application/x-www-form-urlencoded";

//请求热门
export function getHot(){
    return axios.get('/hot')
}

//请求歌曲
export function postSong(path){
    return axios.post('/song',{path})
}

// 切换歌曲
export function changeSong(sort){
    return axios.post('/song/get',{sort})
}

// 获取歌手信息
export function postSinger(name){
    return axios.post('/singer',{name})
}

export function SongList(name){
    return axios.post('/singer/getList',{name})
}

// 搜索
export function SearchSong(song){
    return axios.post('/Search',{song})
}

// 收藏
export function postCollect(title,author,onOff){
    return axios.post('/Collect',{title,author,onOff})
}

// 获取收藏
export function getCollect(){
    return axios.get('/Collect/getcollect')
}

// 是否收藏
export function ifCollect(title){
    return axios.post('/Collect/ifCollect',{title})
}