<template>
    <div class="Eversonger">
        <div class="singerbg">
            <img :src="img" alt="">
            <span class="name">{{name}}</span>
            <div class="zhezhao"></div>
        </div>
        <div class="songs">
            <van-cell v-for="item in song" :title="item.title"  :to='/song/+item.title' />
        </div>
    </div>
</template>

<script>
import {postSinger,SongList} from '../api/index'
export default {
    name:'Eversonger',
    data(){
        return{
            singerbg:'',
            img:'',
            name:'',
            song:[],
            to:''
        }
    },
    created(){
        let name=this.$route.params.id
        postSinger(name)
            .then(res=>{
                this.img=res.data.data.img
                this.name=res.data.data.name
            })
        SongList(name)
        .then(res=>{
            this.song=res.data.data
        })

    }
}
</script>

<style lang="less">
.singerbg{
    position: relative;
    width: 100%;
    text-align: center;
}
.singerbg .name{
    position: absolute;
    top: 0px;
    left: 40%;
    display: block;
    padding-top: 5px;
    font-size: 20px;
    font-weight: bold;
    color: #fff;
    z-index: 3;
}
.singerbg img{
    width: 100%;
    height: 400px;
}
.singerbg .zhezhao{
    position: absolute;
    top: 0;
    width: 100%;
    height: 400px;
    background-color: rgba(28, 24, 42, 0.3)
}
.songs{
    width: 100%;
    margin-top: -50px;
}
.van-cell__title{
    color: #fff;
}
</style>